
const initialState = {
  showMenu: false,
  tasks: []
}

export function appReducer(state = initialState, action) {
  switch (action.type) {
    case 'SHOW_MENU':
      return { ...state, showMenu: !state.showMenu }
    case 'GET_TASKS':
      return { ...state, tasks: action.data }
    default:
      return state
  }
}